.page-container {
  box-sizing: content-box;
  padding-left: calc(100vw - 100%);
  max-width: 800px;
  margin: 0 auto;
}

/* @media (max-width: calc(800px + (var(--viewport-padding-x) * 2))) { */
@media (max-width: 840px) {
  .page-container {
    /* max-width: calc(100vw - (var(--viewport-padding-x) * 2)); */
    max-width: calc(100vw - 40px);
  }
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.nowrap {
  white-space: nowrap;
}
